<template>
  <div class="mc">
    <div style="display:inline-block;width:85%;vertical-align: top;">
      <router-view/>
    </div>
    <div class="navbar">
      <button class="navButton" @click="jump('/home')">首页</button>
      <button class="navButton" @click="jump('/editorPage')">编辑</button>
      <button class="navButton" @click="jump('/aboutPage')">关于本站</button>
      <button class="navButton" @click="jump('/businessPage')">商业互吹联系</button>
    </div>
  </div>
</template>
<script>
export default {
  name: 'MC',
  props: {
    userId: String
  },
  data () {
    return {
    }
  },
  methods: {
    jump (param) {
      if (this.$route.path === param) return false
      this.$router.push(param)
    }
  }
}
</script>
<style>
.mc{
  height: 75%;
  width:83%;
  text-align: left;
  color: white;
  display: inline-block;
  padding: 10px;
  padding-bottom: 80px;
  box-sizing: border-box;
}
.navbar{
  opacity: 1;
  width: 10%;
  float: right;
}
.navButton{
  width: 100%;
  height: 60px;
  text-align: center;
  transition: cubic-bezier(0.075, 0.82, 0.165, 1);
  border:0px solid beige;
  margin-right: 10px;
  background: rgb(51, 50, 50,.3);
  filter:alpha(0.5);
  color: #fff8;
  border-radius: 5px;
  box-sizing: content-box;
  margin-top: 30px;
}
.navButton:hover{
  background: linear-gradient(left,white,rgb(162, 201, 247));
  color: #000;
  cursor: pointer;
}
</style>



