<template>
  <div id="home" v-if="flag">
    <div style="text-align:right;padding-right: 20px;padding-top:5px;" >
      <button style="height:30px;" class="RLButton" @click="login()">登录</button>
      <button style="height:30px;" class="RLButton" @click="register()">注册</button>
    </div>
    <sidebar></sidebar>
    <maincontent></maincontent>
    <div class="footer" ><a target="_blank" href="http://beian.miit.gov.cn/" >粤ICP备20014912号</a></div>
  </div>
</template>

<script>
import axios from 'axios'
import Vue from 'vue'
export default {
  name: 'APP',
  data () {
    return {
      userName: '',
      userId: '',
      flag: false,
      isShow: false
    }
  },
  methods: {
    login () {
      // 0是弹出登录界面
      this.$loginOrRegisterFace(0)
      document.body.style.overflow = 'hidden'
    },
    register () {
      // 1是弹出注册界面
      this.$loginOrRegisterFace(1)
      document.body.style.overflow = 'hidden'
    }
  },
  created () {
    let user = window.sessionStorage.getItem('user')
    if (user !== null) {
      user = JSON.parse(user)
      Vue.prototype.$user = user
      this.flag = true
      return
    }
    var loginPathUrl = this.$baseUrl + 'login'
    let form = {
      userId: '0'
    }
    axios({
      data: form,
      url: loginPathUrl,
      method: 'post'
    }).then(resp => {
      if (resp.data['userName'] !== undefined) {
        var user = {
          userName: resp.data['userName'],
          userId: resp.data['userId'],
          sex: resp.data['sex'],
          age: resp.data['age'],
          declaration: resp.data['declaration'],
          userPassword: null
        }
        // sessionstorage 防止刷新
        window.sessionStorage.setItem('user', JSON.stringify(user))
        Vue.prototype.$user = user
      } else {
        this.$toast('container', '服务器发生错误')
      }
      this.flag = true
    }).catch(error => {
      this.$toast('container', '发生错误=>' + error)
    })
  }
}
</script>

<style>
  .RLButton{
    padding: 5px;
    border-radius: 10px;
    background: rgb(43, 42, 42);
    color: white;
    border:1px solid rgb(48, 150, 184);
    transition: all .3s ease 0s;
  }
  .RLButton:hover{
    padding: 10px;
    background: rgb(48, 150, 184);
    cursor: pointer;
  }
  #home {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  width: 100%;
  min-height: 100%;
  height:fit-content;
  position: absolute;
  box-sizing: border-box;
  left: 0px;
  top: 0px;
}
  .footer{
    height: 50px;
    line-height: 50px;
    position: absolute;
    bottom: 0px;
    width: 100%;
    color: rgb(231, 223, 223);
  }
</style>



