<template>
  <div class='toast' v-if="isShow">
    {{message}}
  </div>
</template>

<script>
export default {
  data () {
    return {
      message: '',
      isShow: true
    }
  },
  mounted () {
    setTimeout(() => {
      this.isShow = false
    }, 2000)
  }
}
</script>
<style>
  .toast{
    position: absolute;
    left: 40%;
    top:30%;
    padding: 5px;
    text-align: center;
    background:rgb(158, 140, 175);
    border-radius: 5px;
    z-index: 1000;
    width: max-content;
    animation:toastAnimation 1s;
-webkit-animation:toastAnimation 1s /*Safari and Chrome*/
  }
  @keyframes toastAnimation{
    from {left:10%;background: red;}
    to {left:40%;background: rgb(158, 140, 175);}
  }

  @-webkit-keyframes toastAnimation /*Safari and Chrome*/{
    from {left:10%;}
    to {left:40%;}
  }
</style>



