<template>
  <div v-if="isAready===2">
    <div class="mdPage">
      <h1 class="h">{{info[1]}}</h1>
      <br/>
      <label style="color: blue;width:max-content;padding:3px;">作者：王四九</label>
      <label style="color: green;width:max-content;padding:3px;">发布于 {{info[3]}}</label>
      <button class='upButton' @click="edit()">编辑</button>
      <br/>
      <hr style="opacity: 0.6"/>
      <br/>
      <div v-html="html" class="markdown-body"/>
      <br/>
      <button class='upButton' @click="upAdd" >点赞：{{info[5]}}</button>
    </div>
    <div id='commentEditArea' style="position: relative">
      <textarea v-model="comment" placeholder="留下你的评论....." class="commentInput"/><br/>
    </div>
    <button class="commentButton"  @click="submit()">发表评论</button>
    <div class="comment" v-for = "(v,n,i) of  commentInfo" :key="i">
      <b>{{v[0]}}:</b>
      {{v[1]}} ——{{v[2]}}
    </div>
  </div>
</template>
<script>
import axios from 'axios'
export default {
  name: 'mdPage',
  data () {
    return {
      isAready: 0,
      isToast: false,
      content: String,
      html: String,
      info: Array,
      articleId: String,
      comment: '', // 输入的评论
      commentInfo: []
    }
  },
  created () {
    this.articleId = this.$route.query.articleId
    let url = this.$baseUrl + 'details?articleId=' + this.articleId
    // 获取文章信息
    axios({
      url: url,
      method: 'get'
    }).then(resp => {
      let c = resp.data
      this.info = c.split('&&', 9)
      //  文章信息包含以下八种
      //  articleId + "&&" +
      //  title + "&&" +
      //  summary + "&&" +
      //  date + "&&" +
      //  category + "&&" +
      //  upNum + "&&" +
      //  readNum+ "&&" +
      //  userId+ "&&" +
      //  content;
      this.html = this.$markdownIt.render(this.info[8])
      this.isAready++
    })
    // 获取评论
    url = this.$baseUrl + 'comments?articleId=' + this.articleId
    axios({
      url: url,
      method: 'get'
    }).then(resp => {
      let data = resp.data
      for (const key in data) {
        if (data.hasOwnProperty(key)) {
          this.commentInfo[key] = data[key].split('&&', 3)
          // userName + "&&" +
          // content + "&&" +
          // date ;
        }
      }
      this.isAready++
    })
  },
  methods: {
    edit () {
      this.$router.push({path: '/editorPage',
        query: {info: this.info, content: this.content}})
    },
    submit () {
      console.log(this.comment.length)
      if (this.comment.length === 0) {
        // 自定义弹框
        this.$toast('commentEditArea', '请输入评论内容')
        return
      }
      if (this.comment.length > 60) {
        // 自定义弹框
        this.$toast('commentEditArea', '评论长度不能超过60个字符')
        return
      }
      let date = new Date()
      // 获取当前日期
      let m = date.getMonth() + 1
      let d = date.getDate()
      let y = date.getFullYear()
      m = m > 9 ? m : '0' + m
      d = d > 9 ? d : '0' + d
      // 格式化年月日
      date = y + '年' + m + '月' + d + '日'
      // 发表评论
      let form = {
        'content': this.comment,
        'userName': this.$user.userName,
        'articleId': this.info[0],
        'date': date
      }
      // 新评论
      let newComment = [this.$user.userName, this.comment, date]
      let url = this.$baseUrl + 'comment'
      axios({
        data: form,
        method: 'post',
        url: url
      }).then(resp => {
        this.commentInfo.splice(0, 0, newComment)
        this.comment = ''
      })
    },
    upAdd () {
      let form = {
        'articleId': this.info[0]
      }
      let url = this.$baseUrl + 'upAdd?articleId=' + this.info[0]
      axios({
        data: form,
        method: 'get',
        url: url
      }).then(resp => {
        let num = Number(this.info[5]) + 1
        this.info.splice(5, 1, num)
      })
    }
  }
}
</script>
<style>
.mdPage{
  width: 90%;
  min-height: 100%;
  margin-top: 20px;
  padding: 5%;
  padding-bottom: 15px;
  background: rgb(255, 255, 255);
  color: black;
}
.upButton{
  padding: 5px;
  padding-left: 10px;
  padding-right: 10px;
  background: rgb(243, 236, 236);
  border: 0px;
}
.upButton:hover{
  cursor: pointer;
  background: rgb(245, 217, 217);
}
.h{
  word-break: break-all;
  word-wrap: break-word;
}
.comment{
  background: white;
  margin-top: 10px;
  padding: 5px;
  width: max-content;
  max-width: 90%;
  color: black;
  border-radius: 5px;
}
.comment:hover{
  background:  rgb(202, 199, 199);
}
.commentInput{
  width:95%;
  resize: none;
  box-sizing: border-box;
  font-size: 15px;
  padding: 5px;
  border-radius: 5px;
  margin:0px;
  margin-top: 10px;
}
  .commentButton{
    margin-top: 10px;
    padding: 5px;
    border-radius: 10px;
    background: rgb(43, 42, 42);
    color: white;
    border:1px solid rgb(48, 150, 184);
    transition: all .3s ease 0s;
  }
  .commentButton:hover{
    padding: 10px;
    background: rgb(48, 150, 184);
    cursor: pointer;
  }
</style>



