<template>
  <div v-if = "isAlready" class="homePage">
    <articlepage v-for = "(value, name,i) in data" :key='i' :info='value' ></articlepage>
  </div>
</template>
<script>
import axios from 'axios'
export default {
  name: 'homepage',
  data () {
    return {
      data: null,
      isAlready: false,
      test: 1
    }
  },
  created () {
    var urlPath = this.$baseUrl + 'getArticles'
    axios({
      url: urlPath,
      method: 'get'
    }).then(resp => {
      this.isAlready = true
      this.data = resp.data
    })
  }
}
</script>
<style>
.homePage{
  width:100%;
}
</style>



